(
	function( $ ) {
		'use strict';

		var marQueeInit = function( $scope, $ ) {
			var $marQuee = $scope.find( '.minimog-marquee-list' ),
				$inner = $( '.minimog-marquee-list__inner', $marQuee ),
				$item = $inner.find( '.item' );

			var	marQueeWidth = $marQuee.outerWidth( true ),
				innerWidth = 0,
				speed = parseInt( $marQuee.data( 'duration' ) ),
				dir = 'left',
				totalWidth;

			$item.each(function () {
				innerWidth += $(this).outerWidth( true ) + 5;/*+5 for some missing px*/
			});

			$inner.width(innerWidth);
			totalWidth = innerWidth + marQueeWidth;

			var ratioWidth = totalWidth / innerWidth,
				newSpeed = speed * ratioWidth;

			function run( pos, speed) {
				$inner.animate(
					{ [dir]: '-=' + pos },
					speed,
					'linear',
					function () {
						$inner.css({
							[dir]: marQueeWidth
						});
						run( totalWidth, newSpeed );
					}
				);
			}

			if ( speed > 0 ) {
				run( innerWidth, speed );
			}
		};

		$( window ).on( 'elementor/frontend/init', function() {
			elementorFrontend.hooks.addAction( 'frontend/element_ready/tm-marquee-list.default', marQueeInit );
		} );
	}
)( jQuery );
